import React from "react";
import { List, Datagrid, TextField } from "react-admin";

export const MyAccount = props => (
  <List {...props} title="Akun Saya">
    <Datagrid>
      <TextField source="id" />
      <TextField source="title" />
      <TextField source="body" />
    </Datagrid>
  </List>
);
