// in src/myondemand.js
import React, { Fragment } from "react";
import {
  List,
  Responsive,
  SimpleList,
  Edit,
  Create,
  Datagrid,
  ReferenceField,
  TextField,
  EditButton,
  DisabledInput,
  Filter,
  ReferenceInput,
  SelectInput,
  SimpleForm,
  TextInput,
  BulkDeleteButton,
  Show,
  SimpleShowLayout,
  RichTextField,
  TabbedForm,
  FormTab
} from "react-admin";
import ResetViewsButton from "./resetviewsbutton";
import RichTextInput from "ra-input-rich-text";
import { DateInput, DateTimeInput } from "react-admin-date-inputs";

export const PostFilter = props => (
  <Filter {...props}>
    <TextInput label="Cari Permintaan" source="q" alwaysOn />
    <ReferenceInput source="customerId" reference="customers" allowEmpty>
      <SelectInput optionText="name" />
    </ReferenceInput>
  </Filter>
);

const OndBulkActionButtons = props => (
  <Fragment>
    <ResetViewsButton label="Reset Views" {...props} />
    {/* Add the default bulk delete action */}
    <BulkDeleteButton {...props} />
  </Fragment>
);

export const PostList = props => (
  <List
    {...props}
    filters={<PostFilter />}
    bulkActionButtons={<OndBulkActionButtons />}
    title="Permintaan"
  >
    <Responsive
      small={
        <SimpleList
          primaryText={record => record.id}
          secondaryText={record => record.type}
          tertiaryText={record => record.quantity}
        />
      }
      medium={
        <Datagrid>
          <TextField source="id" />
          <ReferenceField
            label="Users"
            source="customerId"
            reference="customers"
          >
            <TextField source="name" />
          </ReferenceField>
          <TextField source="type" />
          <TextField source="quantity" />
          <ReferenceField
            label="Pricing"
            source="pricingId"
            reference="pricing"
          >
            <TextField source="price" />
          </ReferenceField>
          <RichTextField source="date" />
          <TextField source="comment" />
          <EditButton />
        </Datagrid>
      }
    />
  </List>
);

const PostTitle = ({ record }) => {
  return <span>Post {record ? `"${record.id}"` : ""}</span>;
};

export const PostEdit = props => (
  <Edit title={<PostTitle />} {...props}>
    <TabbedForm>
      <FormTab label="pengguna">
        <DisabledInput source="id" />
        <ReferenceInput source="customerId" reference="customers">
          <SelectInput optionText="name" />
        </ReferenceInput>
      </FormTab>
      <FormTab label="barang">
        <TextInput source="type" />
        <TextInput source="quantity" />
      </FormTab>
      <FormTab label="keterangan">
        <DateTimeInput
          source="date"
          label="date"
          options={{
            format: "dd/MM/yyyy, HH:mm:ss",
            ampm: false,
            clearable: true
          }}
        />
        <RichTextInput source="comment" />
      </FormTab>
    </TabbedForm>
  </Edit>
);

export const PostCreate = props => (
  <Create {...props}>
    <SimpleForm>
      <ReferenceInput label="User" source="customerId" reference="customers">
        <SelectInput optionText="name" />
      </ReferenceInput>
      <TextInput source="type" />
      <TextInput source="quantity" />
      <DateInput
        source="date"
        label="date"
        options={{ format: "DD/MM/YYYY" }}
      />
      <RichTextInput source="comment" />
    </SimpleForm>
  </Create>
);

export const PostShow = props => (
  <Show {...props}>
    <SimpleShowLayout>
      <TextField source="id" />
      <TextField source="type" />
      <TextField source="quantity" />
      <TextField source="date" />
      <RichTextField source="comment" />
    </SimpleShowLayout>
  </Show>
);
